import React from 'react';
import ListPage from '../../base/ListPage.js';
import less from './AccountManage.less';
import APILXD from '../../../http/APILXD.js';
import ModalDel from '../../../components/modalDelete/ModalDelete.js';
import {Modal,message} from 'antd';
import HttpTool from "../../../tool/HttpTool";
const ModalDelete = new ModalDel();
class AccountList extends ListPage {
    constructor(props) {
        super(props);
    }

    base_getListConfig() {
        return {
            table: {
                columns: this.getTableColumns(),
                url: APILXD.getAdminList,
                otherParam: null,
            },
            new: {
                defaultValue: '',
                action: () => {
                    this.props.openTab({
                        path: 'AddAccount',
                        title: '',
                        post: {
                            parent: this.props
                        }
                    })
                }
            }
        }
    }

    changeAccountStatus(id,status){
        let param = {
            id,
            status,
        };
        let successCB = (code,msg,json,option)=>{
            this.setState({loading:false},()=>{
                message.success('');
                this._loadDataForNet();
            });
        };
        let failureCB = (code,msg)=>{
            this.setState({loading:false},()=>{
                message.error(msg);
            });
        };

        this.setState({loading:true});

        HttpTool.post(APILXD.enableAdmin,successCB,failureCB,param);
    }


    /**
     *  
     */
    getTableColumns() {
        return [
            {
                title: '',
                dataIndex: 'name',
                key: 'name',
            },
            {
                title: 'ip',
                dataIndex: 'ip',
                key: 'ip',
            },
            {
                title: '',
                dataIndex: 'status',
                key: 'status',
                render:(text)=>{
                    return text==1?<span style={{color:'green'}}></span>:<span style={{color:'red'}}></span>
                }
            },
            {
                title: '',
                width: 250,
                render: (text, record, index) => {
                    return (<div>
                        {
                            this.powerConfig.Edit
                                ?(  <div
                                    className={record.status==1?less.btnDelete:less.btnEdit}
                                    onClick={() => {
                                        let newStatus = record.status==1?2:1;
                                        Modal.confirm({
                                            title:'',
                                            content:<div>
                                                <span> </span>
                                                {record.status==1?<span style={{color:'red'}}></span>:<span style={{color:'green'}}></span>}
                                                <span> {record.name} ?</span>
                                            </div>,
                                            onOk:()=>{this.changeAccountStatus(record.id,newStatus)}
                                        })
                                    }}
                                >{record.status==1?'':''}
                                </div>)
                                :null
                        }
                        {
                            this.powerConfig.Edit
                                ?(  <div
                                    className={less.btnEdit}
                                    onClick={() => {
                                        this.props.openTab({
                                            path: 'AddAccount',
                                            title: '',
                                            post: {
                                                id: record.id,
                                                parent: this.props
                                            }
                                        })
                                    }}
                                >
                                </div>)
                                :null
                        }
                        {
                            this.powerConfig.Delete
                            ?(<div
                                    className={less.btnDelete}
                                    onClick={() => {
                                        ModalDelete.show({
                                                title: "",
                                                okTitle: "",
                                                closeTitle: "",
                                            },
                                            {}
                                            ,
                                            {
                                                otherParam: {id: record.id},
                                                content: record.name,
                                                url: APILXD.delAdmin,
                                                apiType: 'post',
                                                tip: '',
                                                callBack: (state) => {
                                                    //
                                                    state === "success" && this._loadDataForNet();
                                                }
                                            });
                                    }}
                                >
                                </div>)
                            :null
                        }
                    </div>)
                }
            },
        ]
    }
}

export default AccountList;